    showRouterView() {
      this.showRouterViewYn = false;
    },
    updateMenuPath(selectedMainMenuNames, selectedMainMenuRouter, selectedSubMenuNames, selectedSubMenuRouter) {
      this.showRouterViewYn = true;
      // console.log('slectedMenuNames :: ', slectedMenuNames);
      this.menuSelectedPath[0] = selectedMainMenuNames;
      this.menuSelectedPath[1] = selectedMainMenuRouter;

      this.menuSelectedPath[2] = selectedSubMenuNames;
      this.menuSelectedPath[3] = selectedSubMenuRouter;

      if( selectedSubMenuNames == '') {
        this.subMenuYn = false;
      } else{
        this.subMenuYn = true;
      }
    },
    openModalView() {
      this.openModal = true;
    },
    closeModalView(data) {
      this.openModal = data
